import { memo, useMemo, useState } from "react";
import { Table } from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import { changeName, countingAge } from "../store/userSlice";
import { increaseStock } from "../store";

// function 함수(){ return 반복문 10억번 돌린 결과 }
function Cart() {
  let state = useSelector(state => state);
  let dispatch = useDispatch();
  let [count, setCount] = useState(0);

  // let result = 함수(); // 10억번 돌린 결과값, 렌더링할 때마다 반복문 실행됨-> 비효율적

  // useMemo( () => {
  //  return 함수()
  // }, [state]) // state가 변할 때만 함수 실행, count가 변할 때는 실행 안함

  return (
    <div>
      <h6>
        {state.user.age} {state.user.name}의 장바구니
      </h6>
      <button
        onClick={() => {
          dispatch(countingAge(10));
        }}
      >
        나이
      </button>
      <Table>
        <thead>
          <tr>
            <th>#</th>
            <th>상품명</th>
            <th>수량</th>
            <th>변경하기</th>
          </tr>
        </thead>
        <tbody>
          {state.cart.map((item, i) => (
            <tr key={i}>
              <td>{state.cart[i].id}</td>
              <td>{state.cart[i].name}</td>
              <td>{state.cart[i].count}</td>
              <td>
                <button
                  onClick={() => {
                    dispatch(increaseStock(item.id)); // a.id 전달
                  }}
                >
                  +
                </button>
              </td>
            </tr>
          ))}
        </tbody>
      </Table>
    </div>
  );
}

export default Cart;
